/* Header.css */
.header {
    text-align: center;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #111;
    padding: 3.472vw 2vw;
    position: relative;
  }
  
  .logo img {
    position: absolute;
    width: 9.067vw;
    top: 0;
  }

  .nav-items{
    margin: 0 auto;
  }
  
  .nav-items a {
    color: white;
    margin: 0 1.5vw;
    text-decoration: none;
    font-weight: 200;
    font-size: 1.244vw;
    font-family: 'Manrope-Regular';
  }
  
  .nav-items a:hover {
    color: #ccc;
  }
  