/* ActorsInfo.css */
.actors-info-page {
    padding: 2vw;
    background-color: #111;
    color: white;
  }
  
.actors-info-page-content{
  display: flex;
  justify-content: space-between;
  padding-left: 22vw;
  padding-right: 2vw;
  position: relative;
}
  
.actor-info h2 {
    text-align: left;
    font-size: 4.974vw;
    padding: 0;
    margin: 0;
}

.actor-info h3 {
  text-align: left;
  font-size: 1.1vw;
  padding: 0;
  margin: 0;
  font-family: 'Manrope-Regular';
  font-weight: 600;
  padding-top: 1vw;
  padding-bottom: 3vw;
  text-transform: uppercase;
}

.actor-info h4 {
  text-align: left;
  font-size: 1.05vw;
  padding: 0;
  margin: 0;
  font-family: 'Manrope-Regular';
  font-weight: 600;
  padding-top: 2vw;
  text-transform: uppercase;
}

.actor-info p {
  text-align: left;
  font-size: 1.036vw;
  padding: 0;
  margin: 0;
  font-family: 'Manrope-Regular';
  font-weight: 300;
  padding-bottom: 2vw;
  text-transform: uppercase;
}
  
.social-icons img {
  width: 4.093vw;
  margin: 0;
  margin-right: 1vw;
}

.social-number {
  padding-top: 10.5vw;
}

.social-number p{
  font-size: 1.3vw;
  padding: 0;
  margin: 0;
  font-family: 'Manrope-Regular';
  font-weight: 600;
  text-transform: uppercase;
}


.actor-info-details{
  display: flex;
  color: #fff;
  justify-content: space-between;
  padding: 0 4;
  padding-left: 1vw;
  position: relative;
}

.actor-info-film{
  border-right: 1px solid #fff;
  padding-right: 6vw ;
  padding-bottom: 10vw;
  margin-bottom: 4vw;
}

.actor-info-film h2{
  text-align: right;
  font-size: 2.487vw;
  padding: 0;
  margin: 0;
}

.actor-info-film h3{
  font-weight: 600;
  font-size: 1vw;
  padding-left: 18vw;
}

.actor-info-film p{
  width: 25vw;
  font-weight: 300;
  font-size: 0.829vw;
  padding-left: 18vw;
}


.actor-info-nav h2{
  text-align: left;
  font-size: 2.487vw;
  padding: 0;
  margin: 0;
  
}

.actor-info-nav  h3{
  font-weight: 600;
  font-size: 1vw;
}

.actor-info-nav  p{
  width: 25vw;
  font-weight: 300;
  font-size: 0.829vw;
  margin: 0;
  padding: 0;
}

.actor-info-nav {
  padding-right: 18vw;
}

  
  .actor-photo img {
    width: 20vw;
    height: auto;
    margin-right: 2vw;
  }
  
  .actor-info {
    font-size: 1vw;
  }
  
  
  .gallery_info {
    margin-bottom: 1vw;
    display: flex;
    flex-direction: column;
    color: #fff;
    position: relative;
  }
  
  .gallery_info h2 {
    font-size: 4.974vw;
    margin-bottom: 1vw;
    color: #fff;
  }
  
  .gallery-images_info {
    display: flex;
    flex-wrap: wrap;
  }
  
  .gallery-images_info img {
    width: 33.21%;
    height: auto;
  }

  img.horizontal{
    width: 50%;
  }

  img.vertical{
    width: 33.31vw;
  }

  .video{
    display: flex;
    position: relative;
  }
  
  .video h2 {
    display: flex;
    font-size: 4.974vw;
    text-align: center;
    align-items: center;
    margin: 0;
    padding: 0;
    color: #fff;
    padding-left: 4vw;
  }
  
  .video img {
    width: 50.933vw;
    height: auto;
    padding: 4vw;
  }

img.actor-info_absolute_imagas{
