/* Actors.css */
.actors-page {
    display: flex;
    background-color: #111;
    color: white;
  }
  
  .sidebar {
    width: 20vw;
    padding: 0 5vw;
    background-color: #111;
  }
  
  .search-input {
    width: 100%;
    padding: 1vw;
    margin-bottom: 1vw;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #fff;
    color: white;
    margin-left: 0.5vw;
    margin-top: 3.5vw;
  }

  .search-input:focus {
    outline: none;
  }
  
  .nav-menu ul {
    list-style: none;
    padding: 0;
  }
  
  .nav-menu li {
    margin: 1vw 0;
    font-size: 1.2vw;
    font-family: 'Manrope-Regular';
    font-weight: 300;
  }
  
  .duck-image img {
    width: 10vw;
    position: absolute;
    top: 42vw;
    left: 16vw;
  }
  
  .content {
    flex-grow: 1;
    padding: 0 3vw 0 4vw;
  }
  
  .filters {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 2vw;
  }
  
  .filter-item {
    font-size: 1.5vw;
    padding-right: 10vw;
  }
  
  .gallery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2vw;
    padding-bottom: 6vw;
  }
  
  .gallery-item {
    background-color: #e0e0e0;
    height: 30vw;
  }
  
  .gallery-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .filter-item_content{
    display: flex;
  }

  .arrows{
    display: flex;
    padding-top: 1vw;
  }

  .arrows_content{
    display: flex;
    flex-direction: column;
    padding-right: 1vw;
    padding-top: 0.5vw;
    padding-left: 0.5vw;
  }

  .arrows_content_bottom img{
    width: 1vw;
    padding-left: 0.5vw;
  }

  .arrows_content img{
    width: 0.674vw;
    padding-bottom: 0.2vw;
  }